/* global http */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Form, Input, Button, Tooltip, Icon } from 'antd';
import './style.scss';
const FormItem = Form.Item;
const { TextArea } = Input;
class ProjectForm extends Component {
  state = {
    submitLoading: false
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields(async(err, formData) => {
      if (!err) {
        this.setState({
          submitLoading: true
        });
        const resp = await http.post('/api/project/add', formData);
        this.setState({
          submitLoading: false
        });
        if (resp.success) {
          this.props.refreshList('create');
          this.props.closeModal();
        }
      }
    });
  }
  validBaseUrl = (rule, value, callback) => {
    if (/^\/[a-zA-Z0-9]+$/.test(value)) {
      callback();
    } else {
      callback('/shop01'); // eslint-disable-line
    }
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="modal-form">
        <FormItem label="">
          {getFieldDecorator('name', {
            rules: [{ required: true, message: '' }, { max: 20, message: '20' }]
          })(
            <Input AUTOCOMPLETE="off" placeholder="" />
          )}
        </FormItem>
        <FormItem label={(
          <span>&nbsp;
            <Tooltip placement="right" title="//shop01/">
              <Icon type="question-circle-o" />
            </Tooltip>
          </span>
        )}>
          {getFieldDecorator('baseurl', {
            rules: [
              { required: true, message: '' }, { max: 20, message: '20' },
              { validator: this.validBaseUrl }
            ]
          })(
            <Input AUTOCOMPLETE="off" placeholder="" />
          )}
        </FormItem>
        <FormItem label="">
          {getFieldDecorator('description', {
            rules: [{ required: true, message: '' }]
          })(
            <TextArea rows={4} placeholder="" />
          )}
        </FormItem>
        <FormItem style={{ marginTop: '15px', textAlign: 'center' }}>
          <Button style={{ width: '120px' }} loading={this.state.submitLoading} type="primary" htmlType="submit">
            
          </Button>
        </FormItem>
      </Form>
    );
  }
};
const ProjectFormWithRouter = withRouter(ProjectForm);
export default ProjectFormWithRouter;
